













import {
	mapState
} from 'vuex'
export default {
	data() {
		return {
			merchant: -1,
			img: '',
			text1: '',
			text2: ''
		}
	},
	computed: mapState(['user']),
	onLoad: function() {
		const market = uni.getStorageSync('market');
		if(market.isMarket){
			uni.redirectTo({
				url: './manage'
			});
		}else{
			uni.startPullDownRefresh();
		}
	},
	onPullDownRefresh() {
		uni.request({
			url: this.GLOBAL.serverSrc + 'market/index/getmerchant',
			method: 'GET',
			data: {
				user_id: this.user.id
			},
			success: res => {
				if (res.data.status === 200) {
					this.merchant = res.data.merchant;
					if (this.merchant === 1) {
						var market = {
							isMarket:true,
							marketId:res.data.market_id
						};
						uni.setStorage({
							key: 'market',
							data: market
						});
						uni.redirectTo({
							url: './manage?market_id=' + market.marketId
						});
					} else if (this.merchant === 2) {
						this.img = 'https://yuange666.oss-cn-beijing.aliyuncs.com/app/pageinfo/ok.png';
						this.text1 = '店铺信息审核中';
						this.text2 = '我们将会在1个工作日内完成审核';
					} else {
						this.img = 'https://yuange666.oss-cn-beijing.aliyuncs.com/app/pageinfo/noShop.png',
							this.text1 = '亲，您还没有开通店铺',
							this.text2 = '如果您拥有超市可以点击下方按钮申请开通'
					}
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: "none"
					});
				}
			},
			fail: (e) => {
				this.GLOBAL.requestFail(e);
				this.img = 'https://yuange666.oss-cn-beijing.aliyuncs.com/app/pageinfo/noShop.png';
				this.text1 = '网络错误,请检查网络';
				this.text2 = JSON.stringify(e);
			},
			complete: () => {
				uni.stopPullDownRefresh();
			}
		});
	},
	methods: {
		goRegMarketInfo() {
			uni.navigateTo({
				url: './regMarketInfo'
			});
		}
	}
}
